@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width){
    @content;
  }
}

@mixin content-padding-inline{
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    max-width: 1020px;
    padding-inline: 120px;
    margin-inline: auto;
  }
}


@mixin hover($property, $toValue) {
  transition: #{$property} $effect-duratiion;
  &:hover{
    #{$property}: $toValue;
  }

}

@mixin page-grid {
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }

}

@mixin easy-out{
  &:hover{
    transition: all 0.3s ease-in-out;
  }
}


@mixin active-link{
  &:hover{
    color: #9393A3;
  }

}
